import React, { Component } from 'react';
import './App.scss';
import Menu from './Menu';
import bread from './bread.json';

class App extends Component {
  render() {
    const breadList = bread;
    return (
      <div className="menulist">
          <h1>이디야제과</h1>
          <ul className="cf">
            {
              breadList.map( data => <Menu picture={data.image} name={data.name} key={data.id} /> )
            }
          </ul>
      </div>
    );
  }
}

export default App;